<template>
    <div v-if="showModal">
        <transition name="fade">
            <div class="modal-mask">
                <div class="modal-wrapper">
                    <div class="modal-container">
                        <div class="modal-header">
                            <h3 class="title">{{ displayTitle }}</h3>
                            <button class="closeBtn" @click="closedialog"></button></button>
                        </div>

                        <div class="modal-body">
                            <div class="imgGroup" v-for="(pic, index) in displayImg" :class="'direction' + imgdirection[index]">
                                <transition name="fade">
                                    <img :src="pic" v-on:load="imgOnLoaded" v-show="imgLoaded"/>
                                </transition>
                                <p>
                                    {{picText[index]}}
                                    <span v-if="imgResouce !== ''">{{ imgResouce }}</span>
                                </p>
                            </div>
                            <div class="textGroup" v-html="displayText"></div>
                            <div class="subTextGroup" v-html="displaySubText"></div>

                        </div>
                    </div>
                </div>
            </div>
        </transition>
    </div>
</template>
<script>
export default {
    props: ["show", "type"],
    data() {
        return {
            showModal: this.show,
            displayTitle:'',
            displayImg:[],
            imgdirection:[],
            imgResouce:'',
            imgLoaded: false,
            picText:[],
            displayText:'',
            displaySubText:''
        };
    },
    watch: {
        show: function () {
            this.showModal = this.show;
            this.updatedialog();
        }
    },
    methods: {
        closedialog() {
            this.showModal = false;
            this.$emit("close");
        },
        imgOnLoaded() {
            this.imgLoaded = true;
        },
        updatedialog() {
            var vm = this;
            switch(this.type) {
                case 1:
                    vm.displayTitle = '';
                    vm.displayImg = [require('./assets/1-1.jpg'), require('./assets/1-2.jpg'), require('./assets/1-4.jpg'), require('./assets/1-3.jpg')];
                    vm.imgdirection = [1,1,0,0];
                    vm.imgResouce = '';
                    vm.picText = ['', '', '', ''];
                    vm.displayText = '<p>()5502</p><p></p>';
                    vm.displaySubText = '<h6></h6><p><a href="https://tour.ntpc.gov.tw/zh-tw/Attraction/Detail?wnd_id=60&id=110748"></a><a href="https://tour.ntpc.gov.tw/zh-tw/Attraction/Detail?wnd_id=60&id=110802"></a></p><h6></h6><p><a href="https://theinitium.com/article/20171122_pickup_Jiufen/"></a></p>';
                    break;
                case 2:
                    vm.displayTitle = '';
                    vm.displayImg = [require('./assets/2-1.jpg'), require('./assets/2-2.jpg'), require('./assets/2-3.jpg'), require('./assets/2-4.jpg')];
                    vm.imgdirection = [0,0,0,0];
                    vm.imgResouce = '';
                    vm.picText = ['11', '', '', ''];
                    vm.displayText = '<p>()3428.5</p><p></p>';
                    vm.displaySubText = '<h6></h6><p><a href="https://tour.ntpc.gov.tw/zh-tw/Attraction/Detail?wnd_id=60&id=112781"></a></p>';
                    break;
                case 3:
                    vm.displayTitle = '()';
                    vm.displayImg = [require('./assets/3-1.jpg'), require('./assets/3-2.jpg'), require('./assets/3-3.jpg'), require('./assets/3-4.jpg')];
                    vm.imgResouce = '';
                    vm.imgdirection = [0,0,0,0];
                    vm.picText = ['', '', '', ''];
                    vm.displayText = '<p>()662.4</p><p></p>';
                    vm.displaySubText = '<h6></h6><p><a href="https://tour.ntpc.gov.tw/zh-tw/Attraction/Detail?wnd_id=60&id=109630"></a></p><h6></h6><p><a href="https://theinitium.com/article/20171211_pickup_Da-nan/"></a></p>';       
                    break;
                case 4:
                    vm.displayTitle = '';
                    vm.displayImg = [require('./assets/4-1.jpg'), require('./assets/4-2.jpg'), require('./assets/4-3.jpg'), require('./assets/4-4.jpg')];
                    vm.imgResouce = '';
                    vm.imgdirection = [0,0,0,0];
                    vm.picText = ['', '', '', ''];
                    vm.displayText = '<p>5532.5</p><p></p>';
                    vm.displaySubText = '<h6></h6><p><a href="https://tour.ntpc.gov.tw/zh-tw/Food/Detail?wnd_id=59&id=209063"></a></p><h6></h6><p><a href="https://theinitium.com/article/20180501-chashan/"></a></p>';
                    break;
                case 5:
                    vm.displayTitle = '';
                    vm.displayImg = [require('./assets/5-1.jpg'), require('./assets/5-2.jpg'), require('./assets/5-3.jpg'), require('./assets/5-4.jpg')];
                    vm.imgResouce = '';
                    vm.imgdirection = [0,2,2,0];
                    vm.picText = ['', '', '', ''];
                    vm.displayText = '<p>()7373.4</p><p></p>';
                    vm.displaySubText = '<h6></h6><p><a href="https://tour.ntpc.gov.tw/zh-tw/Attraction/Detail?wnd_id=60&id=110037"></a><a href="https://tour.ntpc.gov.tw/zh-tw/Attraction/Detail?wnd_id=60&id=109612"></a></p>';
                    break;
                case 6:
                    vm.displayTitle = '';
                    vm.displayImg = [require('./assets/6-1.jpg'), require('./assets/6-2.jpg'), require('./assets/6-3.jpg')];
                    vm.imgResouce = '';
                    vm.imgdirection = [0,0,0];
                    vm.picText = ['', '2', ''];
                    vm.displayText = '<p>4504.3</p><p></p>';
                    vm.displaySubText = '<h6></h6><p><a href="https://tour.ntpc.gov.tw/zh-tw/Attraction/Detail?wnd_id=60&id=402318"></a></p><h6></h6><p><a href="https://theinitium.com/article/20171122_pickup_Shuangxi/"></a></p>';
                    break;
                case 7:
                    vm.displayTitle = '';
                    vm.displayImg = [require('./assets/7-1.jpg'), require('./assets/7-2.jpg'), require('./assets/7-3.jpg'), require('./assets/7-4.jpg'), require('./assets/7-5.jpg')];
                    vm.imgResouce = '';
                    vm.imgdirection = [0,0,0,0,0];
                    vm.picText = ['', '', '', '', ''];
                    vm.displayText = '<p>2372564059.51903</p><p></p>';
                    vm.displaySubText = '<h6></h6><p><a href="https://tour.ntpc.gov.tw/zh-tw/Attraction/Detail?wnd_id=60&id=110301"></a><a href="https://tour.ntpc.gov.tw/zh-tw/Attraction/Detail?wnd_id=60&id=402118"></a></p>';
                    break;
                case 8:
                    vm.displayTitle = '';
                    vm.displayImg = [require('./assets/8-2.jpg'), require('./assets/8-3.jpg'), require('./assets/8-1.jpg')];
                    vm.imgResouce = '';
                    vm.imgdirection = [1,1,0];
                    vm.picText = ['', '', ''];
                    vm.displayText = '<p>3302.2</p><p></p>';
                    vm.displaySubText = '<h6></h6><p><a href="https://tour.ntpc.gov.tw/zh-tw/Attraction/Detail?wnd_id=60&id=110177"><a><a href="https://tour.ntpc.gov.tw/zh-tw/Attraction/Detail?wnd_id=60&id=402519"></a></p>\
                                        <h6></h6><p><a href="https://theinitium.com/article/20180301_pickup_pinglin/"><a></p>';
                    break;
                case 9:
                    vm.displayTitle = '';
                    vm.displayImg = [require('./assets/9-1.jpg'), require('./assets/9-2.jpg'), require('./assets/9-3.jpg')];
                    vm.imgResouce = '';
                    vm.imgdirection = [0,0,0];
                    vm.picText = ['', '', ''];
                    vm.displayText = '<p>8585.5</p><p>101</p>';
                    vm.displaySubText = '<h6></h6><p><a href="https://tour.ntpc.gov.tw/zh-tw/Attraction/Detail?wnd_id=60&id=109613"></a><a href="https://tour.ntpc.gov.tw/zh-tw/Attraction/Detail?wnd_id=60&id=109987"></a></p>';
                    break;
                case 10:
                    vm.displayTitle = '';
                    vm.displayImg = [require('./assets/10-1.jpg'), require('./assets/10-2.jpg'), require('./assets/10-3.jpg'), require('./assets/10-4.jpg')];
                    vm.imgResouce = '';
                    vm.imgdirection = [0,0,0,0];
                    vm.picText = ['', '', '()', ''];
                    vm.displayText = '<p>2432.9</p><p></p>';
                    vm.displaySubText = '<h6></h6><p><a href="https://tour.ntpc.gov.tw/zh-tw/Attraction/Detail?wnd_id=60&id=110661"></a><a href="https://tour.ntpc.gov.tw/zh-tw/Attraction/Detail?wnd_id=60&id=109616"></a></p>';
                    break;
                case 11:
                    vm.displayTitle = '';
                    vm.displayImg = [require('./assets/11-1.jpg'), require('./assets/11-2.jpg'), require('./assets/11-3.jpg'), require('./assets/11-4.jpg')];
                    vm.imgResouce = '';
                    vm.imgdirection = [0,0,1,1];
                    vm.picText = ['', '', '', ''];
                    vm.displayText = '<p>4602.6</p><p></p>';
                    vm.displaySubText = '<h6></h6><p><a href="https://tour.ntpc.gov.tw/zh-tw/Attraction/Detail?wnd_id=60&id=110883"></a><a href="https://tour.ntpc.gov.tw/zh-tw/Attraction/Detail?wnd_id=60&id=402215"></a></p>';
                    break;
            }
        },
    }
};
</script>



